<template>
    <footer>
        <p>Capital Gym &copy; <span class="date">{{ year = 2023 }}</span></p>
    </footer>
</template>


<script>

    let date = new Date();
    let year = date.getFullYear();
    if (document.querySelector('.date') !== null) {
        document.querySelector('.date').textContent = year;
    }

export default {
        name: 'Footer',
        year: date.getFullYear(),
    }
</script>

<style>

footer {
    background-color: #000;
    padding: 30px 60px;
    color: #fff;
}

</style>